<template> 
  <div id="container">
    <div>
      <form action="">
        <input name="difficulty" v-model="difficulty" @input="setaNumbers" min="0" max="6" type="range">
      </form>
    </div>
    <!--
    <div>
      <div :class="currentNumber == a ? 'curNum' : ''" v-for="a in 9" :key="a" class="numInp" @click="currentNumber = a">{{a}}</div>
    </div>
    -->
    <div id="game">
      <div class="block" v-for="a in aNumbers.length" :key="a">
        <div class="block" v-for="b in aNumbers[a - 1].length" :key="b">
          <span v-if="Math.floor(Math.random()*11) >= difficulty" class="filled">{{ aNumbers[a - 1][b - 1] }}</span>
          <div v-else class="empty" @click="clickHandle(e, {x: a, y: b})"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data: function() {
    return{
      currentNumber: 1,
      aNumbers: [[]],
      blankSpaces: [],
      guessInt: 0,
      numberGuesses: [],
      difficulty: 6,
      difficultyTxt: [
        'number mode',
        'sunday driving',
        'easy',
        'medium',
        'hard',
        'extreme'
      ]
    }
  },
  beforeMount(){
    this.setaNumbers();
  },
  mounted(){
    // Sudoku Comitted
  },
  methods: {
    clickHandle(e, pos){
      this.numberGuesses.push([this.currentNumber, pos])
      this.guessInt++;
      console.log(this.numberGuesses)
      console.log(e, pos)
    },
    setaNumbers(){
      const aNumbers = this.aNumbers || []
      let a = 0

      while(a < 9){
        aNumbers[a] = []
        let b = 0
        while(b < 9){
          if(!a){

            aNumbers[a][b] = ( b || 9 )

          } else {
            let arr = aNumbers[a-1]
            let c = 3

            if(a%3 == 0){
              arr = aNumbers[a-3]
              c = 1
            }

            /*
            switch(b){
                case 5:
                  aNumbers[a] = [...this.shuffle(aNumbers[a])]
                  break;
                case 8:
                  aNumbers[a] = [...aNumbers[a].slice(0, 3), ...this.shuffle(aNumbers[a].slice(3, 9))]
                  break;
              }
              */
            aNumbers[a][b] = arr[(b+c)%9]
          }
          b++
        }
        
        if(!a){
          aNumbers[a] = this.shuffle(aNumbers[a])
        } else {
          // aNumbers[a] = aNumbers[]

          //If the array is the first...
          switch(a){
            case 0:
              // shuffle the whole thing
              aNumbers[a] = this.shuffle(aNumbers[a])
              break;
            case 1:
              aNumbers[a] = [...this.shuffle(aNumbers[a-1].slice(3, 9)), ...aNumbers[a-1].slice(0, 3)]
              // aNumbers[a] = [...this.shuffle([...aNumbers[a].slice(2, 5), ...aNumbers[a-1].slice(0, 2)])]
              break;
            case 2:
              aNumbers[a] = this.checkLast(aNumbers)[a]
              break;
          }

        }

        a++
      }
      // let shuffled;

      // for(let a = 0; a < 2; a++){
        // aNumbers[a] = []

          // this.aNumbers[a] = this.aNumbers[a]);
          /*
          let top = this.aNumbers[a-1].slice(3, 9)
          let mid = [...this.aNumbers[a-1].slice(0,3), ...this.aNumbers[a-1].slice(6,9)]
          let bot = this.aNumbers[a-1].slice(0, 6)
          */

          // console.log(top, mid, bot)
          /*
        for(let b = 0; b < 9; b++){
          aNumbers[a][b] = a ? this.shuffle(aNumbers[a-1].slice(0,6))[b] : (b || 9)
        }
        aNumbers[a] = a ? aNumbers[a] : this.shuffle(aNumbers[a])
        */
        /*
          shuffled = this.shuffle(this.aNumbers[a-1]);
          for(let b = 0; b < 9; b++){
            if(this.aNumbers[a-1][b])
          }
         console.log(this.aNumbers[a-1][b])
        }else{
          shuffled = this.shuffle(this.aNumbers[a]);
        }

        this.aNumbers[a] = shuffled
          */
      // }
    },
    checkLast(array, c = 0){
      const top = [...array[c].slice(0, 3), ...array[c+1].slice(0, 3)]
      const mid = [...array[c].slice(3, 6), ...array[c+1].slice(3, 6)]
      console.log(mid)
      array[c+2] = []
      for(let d = 1; d <= 3; d++){
        for(let e = 1; e <= 9; e++){
          if(d == 1 && !top.includes(e)){
            array[c+2].push(e)
          }
          if(d == 2 && !mid.includes(e)){
            array[c+2].push(e)
          }
          if(d == 3 && !array[c+2].includes(e)){
            array[c+2].push(e)
          }
        }
      }
      return array
    },
    shuffle(array, mode=2){
      /* Sorting */
      switch(mode){
        case 0:
          /* -- No sort */
          return
        case 1:
          /* -- Simple sort */
          return array.sort(() => 0.5 - Math.random())
        case 2:
          /* -- Fischer-Gate algorithm */
          for (let a = array.length - 1; a > 0; a--){
            const b = Math.floor(Math.random() * (a + 1))
            const temp = array[a]
            array[a] = array[b]
            array[b] = temp
          }
          return array
      }
    }
  }
}
</script>

